// insights section

.insights {
    padding-top: 0;
    .section_nav {
        margin-bottom: 20px;
    }
}

// media queries

// --- tablet
@media screen and (min-width: 767.98px) {
    .insights {
        .section_nav {
            margin-bottom: 40px;
        }
    }
}

// --- desktop
@media screen and (min-width: 1169.98px) {
    .insights {
        .section_nav {
            margin-bottom: 70px;
        }
    }
}

@media screen and (min-width: 1365.98px) {
    .insights {
        &_slider {
            margin-left: 400px;
            margin-right: -170px;
        }
    }
}