// post reply section

.post_content {
    &-reply {
        padding-bottom: 80px;

        &_title {
            margin-bottom: 10px;
        }

        &_form {
            flex-direction: column;
            gap: 10px;

            textarea {
                height: 130px;
                padding-top: 7px;
                width: 100%;
            }

            .btn {
                margin-top: 10px;
            }
        }
    }
}

// media queries

// --- mobile landscape
@media screen and (min-width: 567.98px) {
    .post_content {
        &-reply {
            &_form {
                flex-direction: row;
                flex-wrap: wrap;
                gap: 10px;

                .field:not(textarea) {
                    width: calc(50% - 5px);
                }

                textarea {
                    flex-grow: 1;
                }

                .btn {
                    width: 340px;
                }
            }
        }
    }
}

// --- tablet
@media screen and (min-width: 767.98px) {
    .post_content {
        &-reply {
            padding-bottom: 100px;
        }
    }
}

// --- desktop
@media screen and (min-width: 1169.98px) {
    .post_content {
        &-reply {
            padding-bottom: 150px;

            &_title {
                margin-bottom: 15px;
            }
        }
    }
}