// recent section

.recent {
    .insights {
        padding-top: 80px;
    }
}

// media queries

// --- tablet
@media screen and (min-width: 767.98px) {
    .recent {
        .insights {
            padding-top: 100px;
        }
    }
}

// --- desktop
@media screen and (min-width: 1169.98px) {
    .recent {
        .insights {
            padding-top: 150px;
        }
    }
}